import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    nodeInfoArray: [
      {
        title: "Upload Speed",
        field: "Coming Soon"
      },
      {
        title: "Status",
        field: "Coming Soon"
      },
    ],
    accountInfoArray: [
      {
        title: "Name",
        field: "N/A"
      },
      {
        title: "Email",
        field: "N/A"
      },
      {
        title: "IP Address",
        field: "N/A"
      },
      {
        title: "Status",
        field: "N/A"
      },
      {
        title: "Wallet Address",
        field: "N/A"
      },
    ]
  },
  mutations: {
    updateAccount (state, profile) {
      state.accountInfoArray[0].field = profile.name
      state.accountInfoArray[1].field = profile.email
      state.accountInfoArray[2].field = profile.ip
      state.accountInfoArray[3].field = profile.status
    },
    updateWallet (state, wallet) {
      state.accountInfoArray[4].field = wallet.address
    }
  },
  actions: {
    nodeDataAsync ({ commit }) {
      fetch('http://localhost:3001/api/node/')
      .then(function(response) {
        return response.json()
      })
      .then(function(myJson) {
        fetch('http://localhost:3001/api/node/' + myJson.response + '/data')
        .then(function(response) {
          return response.json()
        })
        .then(function(myJson) {
          commit('updateAccount', myJson.response)
        })
      })
    },
    walletInfoAsync ({ commit }) {
      fetch('http://localhost:3001/api/keystore/wallets')
      .then(function(response) {
        return response.json()
      })
      .then(function(myJson) {
        if (myJson.response.length > 0) {
          commit('updateWallet', myJson.response[0])
        }
      })
    }
  },
  getters: {
    node: state => {
      return state.node
    },
    account: state => {
      return state.account
    }
  }
})
